import { Component, OnInit, Output, EventEmitter, Input, OnChanges } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router, ActivatedRoute, ParamMap } from '@angular/router';
import { MenubarModule, MenuItem } from 'primeng/primeng';
import { Message } from 'primeng/primeng';
@Component({
  selector: 'achievement-task',
  templateUrl: './achievement-task.component.html',
  styleUrls: ['./achievement-task.component.scss'],
  providers: [Commfunc]
})
export class AchievementTaskComponent implements OnInit {
  
  public show: number = 1;
  msgs: Message[] = [];

  constructor(public commfunc: Commfunc,
    public router: Router, ) {

  }
  ngOnInit() {
    this.judgStatu();
  }


  apply(num) {
    this.show = num;


  }

  judgStatu() {

   
    let url = window.location.href;
    let urlSplit = url.split('/');
    
    urlSplit.map(v => {
      if (v === 'performance-analysis') {
        this.show = 1;
      }
      else if (v === 'task-query') {
        this.show = 2;
      }else if(v === 'task-setting'){
        this.show = 3;
      }
    })
  
  }
}